/* Hiding */

.hide1 {@include hide1();}
.hide2 {display: none;}
.hide3 {text-indent: -1000em;}
.hide4 {visibility: hidden;}
.hide5 {visibility: hidden; text-indent: -1000em;}

/* Links */

a {
  font-weight: 300;
  color: $link;
  text-decoration: none;
  transition: color 0.1s, background 0.1s;

  &:hover,
  &:focus {color: black;}

  article div.content & {text-decoration: underline;}
}

/* Headings */

.headings {
  font: 400 1em/1.2 $headingsSerif;
  text-transform: uppercase;
  letter-spacing: 0.025em;
}

h1,h2,h3,h4,h5,h6 {
  @extend .headings;
}
  
h1 {
  font-size: 1.9em;

  span {display: block;}
}

h2 {font-size: 1.9em;}
h3 {font-size: 1.2em;}
h4 {font-size: 1.1em;}

.divide-before {
  position: relative;
  padding-top: $gws;
  
  &:before {
    position: absolute;
    top: 0; left: 0; right: 0;
    height: 1px;
    background: $light;
    content: "";    

    section.data-tools & {background: rgba($light,0.3);}
  }
}

.divide-after {
  &:after {
    display: block;
    margin: 1em auto 1.25em;
    width: 150px;
    height: 2px;
    background: rgba(black, 0.1);
    content: "";
  }
}

.underline {
  border-bottom: 2px solid rgba(black, 0.1);
  padding-bottom: 0.5em;
}

/* Formatting */

.formatting {
  h1,h2,h3,h4,h5,h6 {margin-bottom: 0.8em;}
  p, ul, ol, dl, blockquote, table, figure {margin-bottom: 1.25em;}
  ul, ol {margin-left: 1.2em;}
  ul {list-style: disc;}
  ol {list-style: decimal;}
  
  dl {
    dt {
      margin: 1em 0 0; 
      font-weight: bold; 
      text-transform: uppercase;

      &:first-child {margin-top: 0;}
    }

    dd {margin: 0 0 0 0.5em;}
  }
}

/* General */

acronym {border-bottom: 1px dotted; cursor: help;}
.strikethrough {text-decoration: line-through; opacity: 0.3;}
.small-text {font-size: 0.8em;}
.align-right {text-align: right;}
.align-centre {text-align: center;}
