$sansSerif: "Yantramanav", "Source Sans Pro", "Hevetica", "Geneva", Sans-Serif;
$headingsSerif: "Oswald", "HelveticaNeue-CondensedBold", "Arial Narrow", Sans-Serif;

/* Oswald */

@font-face {
  font-family: 'Oswald';
  font-weight: 400;
  font-style: normal;
  src: url('/assets/fonts/Oswald/Oswald-regular/Oswald-regular.eot');
  src: url('/assets/fonts/Oswald/Oswald-regular/Oswald-regular.eot?#iefix') format('embedded-opentype'),
       local('Oswald Regular'),
       local('Oswald-regular'),
       url('/assets/fonts/Oswald/Oswald-regular/Oswald-regular.woff2') format('woff2'),
       url('/assets/fonts/Oswald/Oswald-regular/Oswald-regular.woff') format('woff'),
       url('/assets/fonts/Oswald/Oswald-regular/Oswald-regular.ttf') format('truetype'),
       url('/assets/fonts/Oswald/Oswald-regular/Oswald-regular.svg#Oswald') format('svg');
}

/* Yantramanav */

@font-face {
  font-family: 'Yantramanav';
  font-weight: 300;
  font-style: normal;
  src: url('/assets/fonts/Yantramanav/Yantramanav-300/Yantramanav-300.eot');
  src: url('/assets/fonts/Yantramanav/Yantramanav-300/Yantramanav-300.eot?#iefix') format('embedded-opentype'),
       local('Yantramanav Light'),
       local('Yantramanav-300'),
       url('/assets/fonts/Yantramanav/Yantramanav-300/Yantramanav-300.woff2') format('woff2'),
       url('/assets/fonts/Yantramanav/Yantramanav-300/Yantramanav-300.woff') format('woff'),
       url('/assets/fonts/Yantramanav/Yantramanav-300/Yantramanav-300.ttf') format('truetype'),
       url('/assets/fonts/Yantramanav/Yantramanav-300/Yantramanav-300.svg#Yantramanav') format('svg');
}

