       url('/assets/fonts/Yantramanav/Yantramanav-700/Yantramanav-700.woff') format('woff'),
       url('/assets/fonts/Yantramanav/Yantramanav-700/Yantramanav-700.ttf') format('truetype'),
       url('/assets/fonts/Yantramanav/Yantramanav-700/Yantramanav-700.svg#Yantramanav') format('svg');
}