/* ------------------------------- */
/* =Structure */ 
/* ------------------------------- */

body {
  background: $light;
  text-align: left;
  font: 300 106.3%/1.4 $sansSerif;
  color: $text;

  // @include desktop-l {font-size: 125%;}
}

.wrap {
  margin: 0 auto;
  padding: 0 $gws;
  max-width: 72.437em;
  box-sizing: border-box;

  @include desktop {padding: 0 $gwl;}
}

.grid {
  display: block;
  margin: 0 (-$gws);

  @include tablet-xs {
    display: flex;
    flex-wrap: wrap;
  }

  > .grid-item {
    flex: 0 1 auto;
    position: relative;
    margin-bottom: $gwl;
    padding: 0 ($gws);
    box-sizing: border-box;
  }

  &.ratio-2s > .grid-item {
    flex-basis: 50%;
    @include desktop {flex-basis: 25%;}
  }

  &.ratio-4s > .grid-item {flex-basis: 50%;}

  &.ratio-5-3 {
    display: block;
    @include tablet-l {display: flex;}
    
    > .grid-item {flex: 1;}
    > :first-child {flex-grow: 1.6667;}
  }

  &.ratio-3-5 {
    display: block;
    @include tablet-l {display: flex;}
    
    > .grid-item {flex: 1;}
    > :last-child {flex-grow: 1.6667;}
  }

  &.ratio-6-2 {
    display: block;
    @include tablet-l {display: flex;}

    > .grid-item {flex: 1;}
    > :first-child {flex-grow: 3;}
  }

  &.ratio-2-6 {
    display: block;
    @include tablet-l {display: flex;}

    > .grid-item {flex: 1;}
    > :last-child {flex-grow: 3;}
  }
}

/* ------------------------------- */
/* =Header */
/* ------------------------------- */

header[role="banner"] {
  position: fixed;
  top: 0; right: 0; left: 0;
  z-index: 1000; 
  background: black;

  h1,h2 {
    float: left;
    position: relative;
    left: 0;
    z-index: 4;
    font: 1.175em $sansSerif;
    text-transform: none;
    letter-spacing: normal;

    @include tablet-l {@include hide1();}

    a {
      display: block;
      position: relative;
      padding: 0.8rem 0;
      font-weight: 700;
      color: white;
      box-sizing: border-box;
      outline: none;
      
      &.current {color: $primary;}

      @include tablet-l {
        &:hover {color: $primary;}
      }
    }
  }

  h2 {
    float: right;
    
    a:before {
      margin-right: 0.5em;
      font-size: 0.75em;
    }
  }
}

/* ------------------------------- */
/* =Navigation */
/* ------------------------------- */

nav[role="navigation"] {
  position: relative;
  z-index: 3;
  text-align: right;

  @include tablet-l {
    position: static;
    text-align: left;
  }

  > ul {
    position: fixed;
    top: -1500px; left: 0; right: 0; bottom: 100%;
    z-index: 3;
    background: black;
    font-size: 1.175em;
    text-align: left;
    opacity: 0;
    transition: opacity 0.25s, top 0s 0.25s, bottom 0s 0.25s, padding 0s 0.25s;

    .nav-open & {
      top: 0; bottom: 0;
      padding-top: 2.5em;
      opacity: 1;
      transition: opacity 0.25s;
    }
    
    @include tablet-l {
      position: relative;
      top: auto; bottom: auto; left: auto; right: auto;
      border-radius: 0;
      padding-top: 0!important; 
      background: none;
      opacity: 1;
    }

    > li {
      margin-left: $gws;

      &:nth-child(1) {
        overflow: hidden;
        max-height: 0;
        visibility: hidden;
        opacity: 0;
      }

      @include tablet-l {
        float: left;
        margin: 0 1.5rem 0 0;

        &:nth-child(1) {
          max-height: none;
          visibility: visible;
          opacity: 1;
        }
      }

      &.tbij {
        &:before {
          display: block;
          margin-top: 0.5em;
          border-top: 2px solid rgba(white,0.1);
          padding-top: 0.5em;
          width: 250px;
          height: 0;
          content: "";
        }

        @include tablet-l {
          float: right;
          margin: 0 0 0 1.5rem;
          &:before {content: none!important;}
        }

        a {
          position: relative;
          padding-right: 85px;

          span.logo {
            position: absolute;
            top: 0; bottom: 0; right: 0;
            width: 73px;
            background: url(logos/tbij.png) no-repeat 50%;
            background-size: 100% auto;

            &:after {
              display: inline-block;
              padding-top: 37.3%;
              content: "";
            }

            span {@include hide1();}
          }
        }
      }
    }

    a {
      position: relative;
      display: inline-block;
      padding: 0.5rem 0;
      font-weight: 700;
      color: white;
      outline: none;
      transition: color 0.1s;

      @include tablet-l {
        display: block;
        padding: 0.8rem 0;
      }

      &:hover,
      &.current {color: $primary;}
    } 
    
    /* Sub nav */
    ul {
      display: none;

      body:not(.home) & {
        display: block;
        font-size: 0.85em;

        @include tablet-l {
          display: block;
          position: absolute;
          left: 0; right: 0;
          border-top: 1px solid rgba($light,0.3);
          background: black;

          &:before,
          &:after {
            position: absolute;
            top: -1px; bottom: 0;
            width: 1000em;
            background: black;
            content: "";
          }

          &:before {right: 100%;}
          &:after {left: 100%;}
        }
      }

      li {
        margin-left: $gws;

        @include tablet-l {
          float: left;
          margin: 0 1.5rem 0 0;
        }
      }
    }
  }
}

/* ------------------------------- */
/* =Main */
/* ------------------------------- */

main[role="main"] {
  margin-top: 6em;
  padding-top: $gws;

  @include desktop {padding-top: $gwl;}

  header h2 {
    margin-bottom: $gws;
    padding-top: $gws;
    color: #41403d;
  }
}

ul.unstyled,
ol.unstyled {
  margin-left: 0;
  li {list-style: none;}
}

/* ------------------------------- */
/* =Footer */
/* ------------------------------- */
  
footer[role="contentinfo"] {
  padding-bottom: $gwl;

  div.wrap > div {
    border-top: 8px solid $mid;
    padding-top: 0.75em;
  }

  .grid .grid-item {margin-bottom: 0;}

  a {color: #62605c;}

  ul {
    a {
      display: block;
      border-bottom: 1px solid $mid;
      padding: 0.4em 0.2em;
      transition: background 0.1s, color 0.1s;

      &:hover {
        background: rgba(black,0.02);
        color: $primary;
      }
    }
  }
}

/* ------------------------------- */
/* =Sections */
/* ------------------------------- */

section.intro {
  position: relative;
  z-index: 1;

  div.wrap {
    position: relative;
    overflow: hidden;
  }

  figure.image {
    img {
      display: block;
      width: 100%;
    }

    @include desktop {
      margin: 0 (-$gwl);
    }
  }

  div.text {
    position: relative;
    z-index: 2;
    margin: 0 0 $gwl;
    color: $light;

    @include desktop {
      position: absolute;
      top: $gwl; left: $gwl; bottom: 0; right: 0;
      z-index: 1;
      width: 40%;
      min-width: 500px;
    }

    header {
      padding: 15px;
      background: rgba(black,0.8);

      h1 {
        font-size: 1em;

        span {
          display: block;
          margin-bottom: 5px;
          width: 129px;
          background: url(logos/tbij.png) no-repeat 50%;
          background-size: 100% auto;

          &:before {
            display: block;
            padding-top: 37.3%;
            content: "";
          }

          span {@include hide1();}
        }

        strong {font-size: 2.8em; font-weight: 400;}
      }
    }

    > div {
      padding: 0 $gws $gws;
      background: rgba(black,0.8);

      @include desktop {
        padding-top: $gws;
        background: rgba(black,0.65);
      }

      a {color: $primary;}

      p {
        margin-bottom: 1em;
        line-height: 1.2;
        font-size: 1.1em;

        &:last-child {margin-bottom: 0;}

        &.statistic {
          .home & {
            border-top: 1px solid rgba($light,0.3);
            padding-top: $gws;
            font-size: 1em;
          }

          span.numbers {
            display: block;
            @extend .headings;
            font-size: 2em;
            .home & {color: $primary;}
          }
        }
      }
    }
  }
}

section.data-tools {
  .home & {
    position: relative;
    z-index: 2;
    
    @include desktop {margin-top: -75px;}
  }

  div.box {
    display: flex;
    align-items: stretch;

    > div {
      flex: 1;
      padding: $gws;
      background: rgba(black,0.8);
      color: $light;

      h2 {
        margin-bottom: 0.75em;
        border-bottom: 2px solid $light;
        padding-bottom: 0.15em;
      }

      p:last-child {margin-bottom: 0;}
    }
  }
}

.country section.featured {
  position: relative;
  z-index: 2;

  @include desktop {
    margin-top: -230px;

    h2 {
      display: inline-block;
      margin-bottom: $gws;
      padding: $gws;
      background: rgba(black,0.65);
      color: $light;
    }
  }
}

section.latest-strike {

  div.grid > div {
    display: flex;
    align-items: stretch;

    &:first-child {
      margin-bottom: 0;
      @include tablet-l {margin-bottom: $gwl;}
    }
  }

  header,div.text {flex: 1;}

  header {
    position: relative;
    z-index: 2;
    padding: $gws;
    background: rgba(black,0.8);
    color: $light;

    h2 {
      margin-bottom: 0.5em;
      font-size: 1em;
      color: $light;
    }

    ul li {
      border-top: 1px solid rgba($light,0.3);
      padding: 0.35em 0;
    }
  }

  div.text {
    position: relative;
    z-index: 1;
    padding: $gws;
    background: white;
    
    @include tablet-l {
      padding-left: 0;

      &:before {
        position: absolute;
        top: 0; bottom: 0; right: 100%;
        width: $gwl;
        background: white;
        content: "";
      }
    }

    p:first-child {margin-bottom: $gws;}
  }
}

section.content-general {

  div.grid.intro-data {

    div.grid-item {
      display: flex;
      align-items: stretch;

      > div {flex: 1;}
    }

    > div:first-child {
      color: $light;

      > div {background: rgba(black,0.6);}

      header {
        padding: 15px;
        background: $dark;
        
        h1 {font-size: 2em; font-weight: 400;}
      }

      div.text {
        padding: 5px $gws $gws;

        ul li {
          border-bottom: 1px solid rgba($light,0.3);
          padding: 0.5em 0;
        }
      }
    }

    div.formatting p {margin-bottom: 1em;}
  }

  div.main {
    margin-bottom: $gwl;
    border-top: 8px solid $mid;
    padding: $gwl;
    background: white;

    div.grid {
      margin-bottom: $gwl;
      border-bottom: 1px solid $light;
      
      div.grid-item {
        padding: 0;
        @include tablet-l {padding: 0 $gws;}
      }

      > div:last-child {padding-right: 0;}
    }

    &.data-results {
      h2 {
        text-align: center;
        font-size: 1.25em;
        color: $headings;

        strong.number {
          display: block;
          margin-bottom: 0.1em;
          font-size: 2.5em;
          font-weight: 400;
        }
      }

      figure {
        img {
          display: block;
          width: 100%;
        }
      }
    }
  }
}

/* ------------------------------- */
/* =Funnels */
/* ------------------------------- */

ul.funnels {

  aside.article-related & {margin-bottom: 0;}

  &.ratio-4s {
    &.grid:not(.joined) li {
      display: flex;
      align-items: stretch;
      a {flex: 1;}
    }
  }

  &.ratio-2s {
    a {
      figure {
        float: none;
        margin: 0 0 $gws;
        max-width: none;
      }
    }
  }

  &.with-stats {
    a {
      z-index: 1;
      padding: $gws $gws 4rem;

      @include tablet-xs {padding-bottom: 5rem;}

      h3 {
        margin-bottom: 0.5em;
        border-bottom: 2px solid $light;
        padding-bottom: 0.25em;
        @extend .headings;
        font-size: 1.625em;
        color: $headings;
      }
    }

    div.stats {
      position: relative;
      z-index: 2;
      margin: -3rem $gws 0;
      padding: $gws;
      background: rgba(black,0.8);
      color: $light;

      h4, li {
        border-bottom: 1px solid rgba($light,0.3);
        padding: 0.35em 0;
      }

      h4 {padding-top: 0;}
    }
  }

  &.joined {
    position: relative;
    margin-bottom: $gwl;

    &:after {
      position: absolute;
      top: 0; bottom: 0; left: $gws; right: $gws;
      z-index: 1;
      border-top: 8px solid $mid;
      background: white;
      content: "";
    }

    li {
      z-index: 2;
      margin-bottom: 0;

      &:before {
        display: block;
        margin: 0 $gws;
        height: 1px;
        background: $light;
        content: "";
      }

      &:nth-child(1):before {
        margin: 0;
        height: 8px;
        background: $mid;
      }
      
      &:nth-child(2):before {
        @include tablet-xs {
          margin: 0;
          height: 8px;
          background: $mid;
        }
      }
      
      &:not(:nth-child(1)):before {
        aside.article-related & {
          position: absolute;
          top: 0; left: $gws; right: $gws;
          z-index: 2;
          margin: 0;
          height: 1px;
          background: $light;
        }
      }

      a {border-top: 0;}
    }

    & ~ div.funnel-nav {
      margin: (-$gwl) 0 $gwl;
      padding: $gws;
      background: white;

      &:before {margin: 0 $gws;}

      &.pagination {
        p,ol,li {
          display: inline-block;
          vertical-align: baseline;
        }

        ol {
          margin-left: 0.25em;

          li {
            margin-right: 0.5em;

            a {
              font-weight: 700;
              color: $primary;
            }
          }
        }
      }
    }
  }

  li {
    position: relative;

    a {
      overflow: hidden;
      display: block;
      position: relative;
      padding: $gws;
      border-top: 8px solid $mid;
      background: white;
      color: $text;
      box-sizing: border-box;
      
      figure {
        margin: 0 0 $gws;

        aside.article-related & {
          @include tablet-xs {
            float: left;
            margin: 0 $gws 0 0;
            width: auto;
          }
        }

        @include tablet-l {
          float: left;
          margin: 0 $gws 0 0;
          max-width: 50%;
        }

        img {
          display: block;
          width: 100%;
        }
      }

      div.text {overflow: hidden;}

      h3 {
        margin-bottom: 0.25em;
        font: 700 1.15em/1.2 $sansSerif;
        text-transform: none;
        letter-spacing: normal;
      }
    }
  }
}

/* ------------------------------- */
/* =Articles */
/* ------------------------------- */

article {
  
  > header {
    margin-bottom: $gwl;

    h1 {
      padding: $gws;
      background: rgba(black,0.8);
      text-transform: none;
      color: $light;
    }

    div.meta {
      padding: 5px $gws $gws;
      background: rgba(black,0.6);
      color: $light;

      p {
        border-bottom: 1px solid rgba($light,0.3);
        padding: 0.5em 0;
      }
    }
  }

  div.content {
    margin-bottom: $gwl;
    border-top: 8px solid $mid;
    padding: $gws;
    background: white;
    font-size: 1.125em;
    
    figure.main {
      margin-bottom: $gwl;

      img {
        display: block;
        width: 100%;
      }

      figcaption {
        display: block;
        border-bottom: 1px solid $light;
        padding: $gws 0;
        font-size: 0.8em;
      }
    }
  }

  footer {
    border-top: 8px solid $mid;
    padding-top: $gwl;
    font-size: 0.8em;

    :last-child {margin-bottom: 0;}
  }
}

/* ------------------------------- */
/* =Side column */
/* ------------------------------- */

aside.site-related {
  transition: margin 0.2s 0.2s;

  header {@include tablet-l {@include hide1();}}

  div.box {
    overflow: hidden;
    margin-bottom: $gwl;
    border-top: 8px solid $mid;
    padding: $gws;
    background: white;

    figure {
      margin: 0 0 $gws;

      @include tablet-xs {
        float: left;
        margin: 0 $gws 0 0;
        width: auto;
      }

      @include tablet-l {
        margin: 0 0 $gws;
        width: 100%;
      }

      img {
        display: block;
        width: 100%;
      }
    }

    div.text {
      overflow: hidden;
      @include tablet-l {overflow: visible;}
    }

    h3 {
      margin-bottom: 0.25em;
      font: 700 1.15em/1.2 $sansSerif;
      text-transform: none;
      letter-spacing: normal;
    }

    p {
      margin-bottom: 0.5em;

      &:last-child {margin-bottom: 0;}
    }
  }
}

/* ------------------------------- */
/* =Forms */
/* ------------------------------- */

section.data-tools fieldset > div.grid > div.grid-item {
  flex: 1 1 auto;
  margin-bottom: 0;

  &.location-dates {flex: 1.75 1 auto;}

  div.grid-item {
    flex: 1;
    margin-bottom: 0;
  }
}

div.form-wrap {
  margin: 0 0 $gws;

  span.label,
  p.label {
    display: block;
    margin: 0 0 0.5em;
    font-weight: 300;
  }

  span.field {
    display: block;
    margin: 0;

    &.custom-select {
      padding: 0;

      &::after {
        border-left: 2px solid white;
        top: 0; bottom: 0; right: 0;
        width: 1.75em;
        background: url('icons/chevron-down.svg') no-repeat 50%;
        background-size: 60% auto;
      }

      select {
        padding-right: 1.75em;
        color: $light;
      }
    }

    input:not([type=checkbox]):not([type=radio]),
    textarea,
    select {width: 100%;}
  }
}

input,
textarea,
select,
button {font: 300 1em/1.0 $sansSerif;}

input:not([type=checkbox]):not([type=radio]):not([type=submit]),
textarea,
.custom-select {
  border: 2px solid $light;
  border-radius: 0;
  padding: 0.2.8em 0.4em 0.2.8em 0.2.8em;
  background: black;
  font: 300 23px/1.0 $sansSerif;
  color: $light;
  box-sizing: border-box;
  box-shadow: none;
  outline: none;
}

input:not([type=checkbox]):not([type=radio]):not([type=submit]),
textarea {-webkit-appearance: none;}

ol.custom-checkbox-buttons {
  line-height: 1.0;

  li {
    margin-bottom: 1em;

    label {
      display: block;
      position: relative;
      padding-left: 35px;
      font-size: 23px;
      white-space: nowrap;
      cursor: pointer;

      input {@include hide1();}

      &:before {
        position: absolute;
        left: 0; top: 0;
        z-index: 1;
        border: 2px solid $light;
        border-radius: 50%;
        width: 20px;
        height: 20px;
        background: black;
        content: "";
      }

      &:after {
        position: absolute;
        left: 6px; top: 6px;
        z-index: 2;
        border-radius: 50%;
        width: 12px;
        height: 12px;
        background: $light;
      }

      &.checked:after {content: "";}
    }
  }
}

