.button { 
  position: relative;
  display: inline-block;
  border: 2px solid $primary;
  padding: 0.4em 0.5em;
  background: white;
  text-align: center;
  font-weight: 700;
  font-size: 1.2em;
  line-height: 1.0;
  color: $primary;
  cursor: pointer;
  outline: none;
  text-decoration: none;
  text-transform: uppercase;
  transition: border-color 0.1s, background 0.1s, color 0.1s;
  -webkit-appearance: none;
 
  @include phone-l {font-size: 1.3em;}

  &:hover {
    border-color: black;
    color: black;
  }
  
  &.small {font-size: 0.9em;}

  &.alt {
    border-color: white;
    background: $primary;
    color: white;

    &:before {border-right-color: white!important;}

    &:hover {
      background: black;
      color: white;
    }
  }

  &.icon-chevron-right {
    padding-left: 2.25em;

    &:before {
      position: absolute;
      top: 0; bottom: 0; left: 0;
      border-right: 2px solid $primary;
      width: 1.75em;
      text-align: center;
      line-height: 1.8em;
      transition: border-color 0.1s;
    }

    &:hover:before {border-right-color: black;}
  }
}
